.wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 1vh 0;
    background-color: rgba(255, 255, 255, 0.2);
    width: 90%;
    height: clamp(3rem, 8vw, 100px);
    max-width: 1536px;
    margin-inline: auto;
    position: relative;
    overflow: hidden;
    mask-image: linear-gradient(
        to left,
        rgba(0, 0, 0, 0),
        rgba(0, 0, 0, 1) 20%,
        rgba(0, 0, 0, 1) 80%,
        rgba(0, 0, 0, 0)
    );
}

.wrapper-paused-hover:hover .item {
    animation-play-state: paused;
}

.wrapper-paused .item {
    animation-play-state: paused;
}

.technology-slider {
    width: clamp(3rem, 7vw, 5rem);
    height: auto;
    color: white;
}

.item {
    border-radius: 6px;
    position: absolute;
    align-items: center;
    left: max(calc(-5rem * var(--amount-technologies)), 100%);
    transition: transform .3s;
    animation-name: scrollLeft;
    animation-duration: var(--transition-slider-duration);
    animation-timing-function: linear;
    animation-iteration-count: infinite;
}

.item:hover {
    transform: scale(1.1);
}


.item:has(img[alt="Express.js-icon"]),
.item:has(img[alt="jQuery-icon"]) {
    .technology-slider {
        filter: invert(1);
    }
}

.item1 {
    animation-delay: calc(var(--transition-slider-duration) / var(--amount-technologies) * (var(--amount-technologies) - var(--item)) * -1);
}
