@keyframes scrollLeft {
  to {
    left: -60rem;
  }
}

@media (max-width: 1024px) {
    .wrapper {
        margin-top: 3rem;
    }

    .item {
        left: max(calc(-3rem * var(--amount-technologies)), 100%);
    }
}

/* Atec */
@media (min-height: 560px) and (max-height: 639px){ 
    .wrapper {
        margin-top: clamp(10vw, 55vh, 15vw);
    }
}

/* TV */
@media (max-height: 559px) {
    .wrapper {
        margin-top: clamp(20vw, 60vh, 25vw);
    }
}